.project-card {
  flex: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 16px 0px;
  padding: 8px 0px;
}
.project-photo {
  height: auto;
  width: 40%;
}

 
.project-title {
  font-size: 26px;
  font-weight: 600;
}

.project-tags {
  display: flex;
  max-width: 60%;
  flex-wrap: wrap;
}
.tag {
  padding: 4px 8px;
  background-color: aquamarine;
  border-radius: 4px;
  margin-right: 6px;
  margin-bottom: 4px;
}
.project-links a {
  text-decoration: none;
  color: black;
}
.project-links {
  margin-top: 6px;
  display: flex;
}
.link-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: max-content;
  background-color: aquamarine;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 16px;
  margin-right: 6px;
}
.link-button i {
  display: flex;
  align-items: center;
  margin-right: 4px;
}
@media only screen and (max-width: 720px) {
  .project-photo {
    width: 70%;
    margin: 1rem;
  }
  .project-card {
    flex-direction: column;
  }
 .project-info {
   width: 90%;
   margin: 1rem;
}
  .project-tags {
    max-width: 100%;
  }
}
